@use '../../scss/abstracts';

.wp-block-md-sutherlandglobal-md-posts {
    .md-posts {
        padding: 20px 0;
        .md-posts__filter {
            .md-posts_total {
                font-size: 30px;
                font-family: "proxima-nova";
                line-height: 40px;
                font-weight: 700;
                color: #2c3546;
                span {
                    color: #df1952;
                    margin-right: 20px;
                }
            }
        }
        .md-posts__grid {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
            padding: 12px 50px 10px 40px;
            .md-posts__grid-item {
                
                .md-posts__grid-item-meta {
                    span {
                        font-size: 16px;
                        line-height: 19px;
                        color: #777;
                        font-family: "proxima-nova";
                        &:not(:last-child)::after {
                            content: "|";
                            margin: 0 10px;
                            line-height: 16px;
                        }
                    }
                }
                .md-posts__grid-item-thumbnail {
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .md-posts__grid-item-title {
                    a {
                        font-size: 20px;
                        line-height: 30px;
                        color: #2c3546;
                        font-weight: 600;
                        font-family: "proxima-nova";
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                }
                &.step_2 {
                    flex-basis: calc(50% - 20px);
                }
                &.step_3 {
                    flex-basis: calc(33.33% - 20px);
                }
            }
        }
        ul.page-numbers {
            padding: 12px 50px 10px 40px;
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
            width: 100%;
            justify-content: center;
            align-items: center;
            li {
                list-style-type: none;
                width: 65px;
                height: 65px;
                background-color: #fff;
                box-shadow: 0px 3px 6px rgba(0, 0, 0, .1607843137);
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 8.5px;
                font-size: 26px;
                line-height: 32px;
                font-weight: 700;
                color: #2c3546;
                a {
                    color: inherit;
                }
                span.current {
                    color: #df1952;
                }
            }
        }
    }
}


.insights-filter-main .insights-filter-dropbox-main {
    display: grid;
    margin: 0 0 30px 0;
    grid-template-columns: minmax(394px, 25%) auto;
    position: sticky;
    top: 68px;
    background: #fff;
    z-index: 9
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-results {
    font-size: 30px;
    font-family: "proxima-nova";
    line-height: 40px;
    font-weight: 700;
    color: #2c3546
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-results span {
    color: #df1952;
    margin-right: 20px
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-results span:empty {
    display: none
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 58px
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper {
    position: relative
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .form-control {
    width: 100%;
    font-size: 18px;
    font-family: "proxima-nova";
    border: none;
    box-sizing: inherit;
    border-bottom: 1px solid #2e3748;
    padding: 0 30px 15px 0;
    background-color: rgba(0,0,0,0);
    text-align: left;
    color: #2e3748;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 311px
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .form-control::after {
    content: "";
    position: absolute;
    right: 10px;
    top: 13px;
    background-image: url(https://newsite.sutherlandglobal.altudo.net/wp-content/themes/sutherland/assets/build/images/select-down-arrow.a46479f1.svg);
    background-repeat: no-repeat;
    background-size: 10.04px 5.99px;
    width: 10.04px;
    height: 5.99px
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes {
    box-shadow: 0px 12px 12px rgba(0,0,0,.2549019608);
    padding: 30px 21px;
    margin-top: 18px;
    background: radial-gradient(closest-side at 50% 50%, #FFFFFF 0%, #FFFFFF 50%, #FFFFFF 100%);
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    top: 100%;
    z-index: 2
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes .inner-wrap label {
    width: 100%;
    display: flex;
    flex-wrap: nowrap
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes .inner-wrap label:not(:last-child) {
    margin-bottom: 12px
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes .inner-wrap label input {
    margin-right: 7px;
    box-shadow: inset 0px 3px 6px rgba(0,0,0,.1607843137);
    width: 20px;
    min-width: 20px;
    height: 20px;
    margin: 0 7px 0 0
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes .inner-wrap label span {
    font-size: 14px;
    font-family: "proxima-nova"
}

.insights-filter-main .insights-filter-dropbox-main .insights-filter-dropbox .category-filter-wrapper .checkboxes::before {
    content: "";
    position: absolute;
    left: 30px;
    bottom: 100%;
    background-image: url(https://newsite.sutherlandglobal.altudo.net/wp-content/themes/sutherland/assets/build/images/menu-arrow-sticky.8a9109e3.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 48px;
    height: 18.78px;
    margin-bottom: -1px
}

.insights-filter-main .insights-filter-dropbox-main {
    grid-template-columns: minmax(250px, 10%) auto;
    padding: 12px 50px 10px 40px;
}
